header {
    height: 20vh;
    width: 100vw;
    max-width: 100%;
    position: fixed;
    display: flex;
    justify-items: center;
    justify-content: center;
    align-items: center;
    background-color: #7C92A1;
    z-index: 2;
}

header img {
    width: 25%;
}

header h1 {
    text-align: center;
    justify-self: start;
    font-size: 2.5vw;
    white-space: nowrap;
}

.header-logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1vw;
}

.header-link {
    text-decoration: none;
    color: black;
}

.header-link:hover {
    color: #93AEBF;
}

.header-link:active {
    color: white;
}

.header-menu-button-inactive {
    position: absolute;
    left: 2%;
}

.header-menu-button-inactive:hover {
    border-radius: 100%;
    background-color: #93AEBF;
    cursor: pointer;
    transform: scale(1.25);
}

.header-menu-button-active {
    position: absolute;
    left: 8%;
    cursor: pointer;
}

.header-menu-button-active:hover {
    border-radius: 100%;
    background-color: #7C92A1;
    transform: scale(1.25);
}

.nav-menu-container-active {
    width: 25%;
    height: 100vh;
    position: fixed;
    top: 0vh;
    left: 0vw;
    padding-left: 0;
    margin-top: 0;
    background-color: #93AEBF;
    z-index: 0;
    transform: translate3d(0, 0, 0);
    transition: transform 1s;
}

.nav-menu-container-inactive {
    width: 25vw;
    height: 100vh;
    position: fixed;
    top: 0vh;
    left: 0vw;
    padding-left: 0;
    margin-top: 0;
    list-style-type: none;
    background-color: #93AEBF;
    z-index: 0;
    transform: translate3d(-25vw, 0, 0);
    transition: transform 1s;
}

.menu-list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    row-gap: 7.5vh;
    padding-top: 5vh;
    list-style-type: none;
    font-size: 1.5rem;
    width: 100%;
}

.menu-title-container {
    height: 20vh;
    background-color: #6A7D8A;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-menu-container li {
    height: auto;
    width: 75%;
}

.nav-link {
    text-decoration: none;
    color: black;
}

.nav-link:hover {
    transform: scale(1.25);
    color:#4D6473;
}

.nav-link:active {
    color: white;
}

.blackout-active {
    height: 100%;
    width: 100vw;
    background-color: black;
    position: absolute;
    right: -100vw;
    opacity: 60%;
}

.blackout-inactive {
    display: none;
}

@media only screen and (max-width: 600px) {

    header {
        height: 15vh;
    }
    header h1 {
        font-size: x-large;
    }

    .header-link:hover {
        color: black;
    }

    .menu-title-container {
        height: 15vh;
    }

    .nav-menu-container-active {
        width: 50vw;
    }

    .nav-menu-container-inactive {
        width: 50vw;
        transform: translate3d(-55vw, 0, 0);
    }

    .nav-link:hover {
        transform: none;
    }

    .navlink:active {
        color: none;
    }

    :hover {
        
    }
}

@media only screen and (min-width: 601px) and (max-width: 1000px) { 
    header h1 {
        font-size: xxx-large;
    }

    .nav-menu-container-active {
        width: 50vw;
    }

    .nav-menu-container-inactive {
        width: 50vw;
        transform: translate3d(-55vw, 0, 0);
    }

    .nav-link:hover {
        transform: none;
    }

    .navlink:active {
        color: none;
    }

    .menu-title-container {
        font-size: x-large;
    }

    .nav-menu-container-active li {
        font-size: xx-large;
    }

    .nav-menu-container-inactive li {
        font-size: xx-large;
    }
}
